@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");
:root {
  --primary: 255,215,0;
  --secondary: 65,105,225;
  --danger: 255, 0, 0;
  --warning: 255, 255, 0;
  --success: 0, 255, 0;
  --info: 0, 0, 255;
  --light: 238, 238, 238;
  --gray: 179, 179, 179;
  --dark: 34, 34, 34;
  --primary-color: rgb(var(--primary));
  --secondary-color: rgb(var(--secondary));
  --danger-color: rgb(var(--danger));
  --warning-color: rgb(var(--warning));
  --success-color: rgb(var(--success));
  --info-color: rgb(var(--info));
  --light-color: rgb(var(--light));
  --gray-color: rgb(var(--gray));
  --dark-color: rgb(var(--dark));
}

:root {
  --fonts: "Open Sans", sans-serif;
  --small: 0.8rem;
  --medium: 1rem;
  --large: 1.2rem;
  --x-large: 1.4rem;
}

:root {
  --header-height: 50px;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a, a:visited, a:hover, a:active {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

body {
  background-color: var(--light-color);
  font-family: var(--fonts);
  height: 100vh;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: var(--header-height);
  transition: background-color 500ms;
  --bg-color: var(--primary-color);
  background-color: var(--bg-color);
}
.header__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.header__top .header__logo {
  font-size: var(--x-large);
  font-weight: 600;
  position: relative;
}
.header__top .header__logo::after {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  width: 75%;
  background: var(--secondary-color);
  height: 3px;
  margin-top: 0px;
  border-radius: 5px;
}
.header__nav {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  background: var(--light-color);
  padding-left: 1.5625rem;
  overflow: hidden;
  max-height: 108px;
  transition: max-height 300ms, border-color 500ms;
}
.header__nav__link {
  font-size: var(--medium);
  font-weight: 500;
  text-decoration: none;
}
.header__nav__link:first-child {
  margin-top: 0.625rem;
}
.header__nav__link:last-child {
  margin-bottom: 0.625rem;
}
.header__nav__link:hover {
  color: var(--secondary-color);
  text-decoration: var(--secondary-color) underline;
}
.header #toggle {
  display: none;
}
.header .toggle-btn {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 30px;
  align-items: flex-end;
}
.header .toggle-btn span {
  height: 4px;
  background: var(--dark-color);
  transition: 0.4s;
  border-radius: 5px;
}
.header .toggle-btn .first {
  width: 90%;
}
.header .toggle-btn .second {
  width: 60%;
}
.header .toggle-btn .third {
  width: 30%;
}
#toggle:not(:checked) ~ .header__nav {
  max-height: 0;
}

.content {
  margin-top: var(--header-height);
}

.container-y {
  padding: 1.5625rem 0;
}

.container {
  padding: 1.5625rem;
}

.container-x {
  padding: 0 1.5625rem;
}

.banner__container {
  height: calc(100vh - var(--header-height));
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.banner__text {
  transform: translateY(-50%);
}
.banner__text h1 {
  font-size: 4.375rem;
  font-weight: 600;
}
.banner__text h2 {
  font-size: 1.875rem;
  font-weight: 300;
  color: var(--secondary-color);
}

.who__am__i {
  background-color: var(--primary-color);
}
.who__am__i p {
  font-size: 1.125rem;
  letter-spacing: 0.0625rem;
  line-height: 1.5rem;
  font-weight: 400;
}
.who__am__i div {
  display: flex;
}
.who__am__i div span {
  display: block;
  width: 7.8125rem;
  text-align: center;
  margin-top: 1.25rem;
  padding: 0.9375rem 0;
  background-color: var(--secondary-color);
  border: var(--dark-color) solid 0.0625rem;
  position: relative;
  z-index: 100;
}

section.projects .projects_wrapper {
  display: flex;
}
section.projects .projects_wrapper .project-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}
section.projects .projects_wrapper .project-card .card-img {
  width: 100%;
}
section.projects .projects_wrapper .project-card .card-text .card-title {
  font-size: var(--large);
}
.title {
  font-size: var(--x-large);
  font-weight: 600;
  margin-bottom: 1.25rem;
}

.outlined {
  position: relative;
}
.outlined::after {
  content: "";
  position: absolute;
  top: 105%;
  left: 0;
  right: 0;
  background-color: var(--secondary-color);
  height: 0.1875rem;
  border-radius: 0.1875rem;
  width: 4ch;
}